import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from '../vendor.service';
import { AuthService } from 'src/app/guards/auth.service';

@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss']
})
export class AddvendorComponent implements OnInit {
  vendor = new Vendor();
  vendors:Vendor[];
  vendorId:any;
  constructor(private router: Router,private route:ActivatedRoute,private _service: VendorService,private authService:AuthService) { }

  ngOnInit() {
    this.getVendorById();
  }

  resetform() {
    this._service.vendor = {
      vendorId: null,
      vendorName: '',
      vendorAddr: '',
      vendorLoc: '',
      vendorService: '',
      vendorPincode: '',
      isActive: '',
      contactID: '',
      contactName: '',
      department: '',
      email: '',
      phone: '',
      
    }
  }


  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['vendor'])
    }, (error)=>{
      console.log(error);
    });
  }
  getVendor():void{
    this._service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);
    },(error)=>{
      console.log(error);
    });
  
  }
 

  getVendorById(){
    this.vendorId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vendorId)
    .subscribe((data)=>{
      this.vendor=data;
      console.log(data);
    },(error)=>{
      console.log(error);
    })
  }
  
  logout(){
    this.authService.logout();
  }
}
