import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from './vendor.service';
import { Vendor } from './vendor';
import { AuthService } from '../guards/auth.service';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {
  view: any;

  constructor(private router: Router, private service: VendorService, private route: ActivatedRoute,private authService:AuthService) { }
  vendors: Vendor[];
  vendor = new Vendor();
  searchText;
  searchid: any;

  ngOnInit() {
    this.getVendors();
  }
  addvendor() {
    this.router.navigate(['addvendor'])
  }
  editVendor(data: any) {
    this.router.navigate(['editVendor', data]);

  }

  getVendors() {
    this.service.getAllVendors()
      .subscribe((staffData) => {
        this.vendors = staffData;
        console.log(staffData);
      }, (error) => {
        console.log(error);
      });
  }

  DisableVendor()
  {
    console.log("inside disable" +this.searchid);
    this.service.disableVendor(this.searchid)
    .subscribe((data)=>{
      console.log(data);
      this.getVendors();
    },(error)=>{
      console.log(error);
    });   
  }
  onSelect(selectedStaff: any) {
    console.log("Selected item Id:", selectedStaff.vendorId);
    this.searchid = selectedStaff.vendorId;
  }
  viewContact(data){
    this.router.navigate(['viewcontact',this.view])
  }

  onSelect1(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendorId);
    this.view = selectedVendor.vendorId;
    this.viewContact;
    this.viewContact(this.view);
  }
  logout(){
    this.authService.logout();
  }
}
