import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { VendorComponent } from './vendor/vendor.component';
import { AddvendorComponent } from './vendor/addvendor/addvendor.component';
import { ContactComponent } from './vendor/contact/contact.component';
import { AuthGuard } from './guards/auth.guard';




const routes: Routes = [
  {
    path:'',
    redirectTo: 'login', pathMatch: 'full'
  },
  {
    path:'login',
    component:LoginComponent

  },
  {path:'vendor',component:VendorComponent, canActivate: [AuthGuard]},
  {path:'addvendor',component:AddvendorComponent, canActivate: [AuthGuard]},
  {path:'editVendor/:id',component:AddvendorComponent, canActivate: [AuthGuard]},
  {path:'viewcontact/:id',component:ContactComponent, canActivate: [AuthGuard]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
